// Start Header




/*
=====
LODEARS
=====
*/
html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 130px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #ec1f23;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0.9;
        transform: translateY(-21%);
        // transform: scale(0.987), translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: translateY(0%);
        // transform: scale(1.1) translateY(0%);
    }
}





  


.owl-carousel .owl-item img {
    width: auto;
}







.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: #00000090;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ec1f23;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      border-radius: 50px;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #ec1f2380;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      [dir="ltr"] & {
        left: unset;
        right: 20px;
      }
      &:hover {
        color: #ec1f23;
      }
    }
  }
}








/*------ upper start ------*/
.upper-bar {
    background-color: #ec1f23;
    padding: 10px 0;
    .zx-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .upper-contact {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      >p {
        margin: 0;
        font-family: reg;
        font-size: 14px;
        color: #ffffff;
        padding: 0 35px 0 0;
        [dir="rtl"] & {
          padding: 0 0 0 35px;
          font-family: ar-reg;
        }
        >i {
          padding: 0 0 0 10px;
          transform: rotateY(180deg);
          [dir="rtl"] & {
            padding: 0 10px 0 0;
          }
        }
      } 
    }
    .upper-search {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      >i {
        font-size: 14px;
        color: #fff;
        padding: 0 10px;
        cursor: pointer;
      }
      >input {
        background-color: transparent;
        outline: none;
        border: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        transition: all .3s ease-in-out;
        width: 223px;
        &::placeholder {
          font-family: reg;
          font-size: 13px;
          color: rgba(255, 255, 255, 0.4);
          [dir="rtl"] & {
            font-family: ar-reg;
          }
        }
        &:hover, &:focus {
          border-bottom: 1px solid #fff;
        }
      }
    }
}
/*------ upper end ------*/





/*------ nav placeholder start ------*/
.nav-place {
  width: 100%;
  height: 111px;
  transition: all .3s ease-in-out;
  display: none;
  &.show {
    display: block;
  }
}
/*------ nav placeholder end ------*/


/*------ navbar start ------*/
.nav-bar {
  height: 111px;
  position: relative;
  background-color: #fff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.07);
  z-index: 9;
  transition: all .5s ease-in-out;
  &.fixed-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 0;
  }
  .logo-container {
    // width: 230px;
    .logo {
      width: 55px;
    }
  }

  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    >li {
      position: relative;
      font-family: md;
      font-size: 15px;
      color: #343433;
      text-transform: uppercase;
      margin: 0 25px 0 0;
      transition: all .3s ease-in-out;
      [dir="rtl"] & {
        margin: 0 0 0 25px;
        font-family: ar-md;
      }
      &:last-child {
        margin: 0;
      }
      &:hover {
        color: #ec1f23;
        .sub-cat-hover {
          opacity: 1;
          visibility: visible;
          top: 30px;
        }
      }
    }
    .sub-cat-hover {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: -20px;
      [dir="rtl"] & {
        left: unset;
        right: -20px;
      }
      // transform: translate
      min-width: 190px;
      min-height: 222px;
      background-color: rgba(236, 31, 35, 0.949);
      // box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
      // border-radius: 10px;
      padding: 7px 10px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      z-index: 9;
      transition: all .5s ease-in-out;
      li {
        width: 100%;
        transition: all .3s ease-in-out;
        font-family: reg;
        font-size: 15px;
        color: #fac0c1;
        border-bottom: 1px solid #ffffff33;
        padding: 7px 9px;
        margin: 0;
        [dir="rtl"] & {
          font-family: ar-reg;
        }
        &:last-child {
          border: none;
        }
        &:hover {
          color: #fff;
        }
      }
    }
  }

  
}


/*------ navbar end ------*/








/*------ header start ------*/
.header {
  // margin: 20px 0 0 0;
  transition: all .3s ease-in-out;
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        right: 100px;
        bottom: 50%;
        transform: translateY(-50%);
        [dir="rtl"] & {
          right: unset;
          left: 100px;
        }


        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .owl-dot {
          width: 12px;
          height: 12px;
          background-color: #fff;
          outline: none;
          margin: 4px 0px;
          &.active {
            background-color: #ec1f23;
          }
        }
      }
    }
   
}

/*------ header end ------*/



/*------ about hos start ------*/
