        display: flex;
        justify-content: center;
        align-items: center;
        padding: 30px;
        font-size: 30px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        transition: all .5s ease-in-out;
        // >i {
        //   font-size: 30px;
        //   width: 70px;
        //   height: 70px;
        //   background-color: #fff;
        //   border-radius: 50%;
        //   // color: transparent;
        //   color: #ec1f23;
        //   transition: all .3s ease-in-out;
        //   display: flex;
        //   justify-content: center;
        //   align-items: center;
        //   padding: 2px 0px 0px 2px;
        // }
        .play-icon {
          img {
            filter: brightness(0%) invert(1);
            transition: all .3s ease-in-out;
          }
        }
      }
      &:hover {
        img {
          transform: scale(1.05);
          // opacity: 0.780;
        }
        .videos-overlay {
          // >i {
          //   background-color: #ec1f23;
          //   color: #ffffff;
          // }
          .play-icon {
            img {
              filter: unset;
            }
          }
        }
      }
    }
  }
