/*------ blog ------*/
.blog-page {
  .blog-page-content {
    margin: 100px 0 150px 0;
    .blog-search {
      width: 100%;
      position: relative;
      margin: 0 0 30px 0;
      >input {
        width: 100%;
        padding: 10px 9px;
        outline: none;
        border: none;
        border-bottom: 1px solid #e6e6e6;
        transition: all .3s ease-in-out;
        &::placeholder {
          font-family: reg;
          font-size: 13px;
          color: rgba(41, 41, 40, 0.4);
          [dir="rtl"] & {
            font-family: ar-reg;
          }
        }
        &:hover, &:focus {
          border-bottom: 1px solid #ec1f23;
        }
      }
      >i {
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        cursor: pointer;
        transition: all .3s ease-in-out;
        font-size: 14px;
        color: rgb(41, 41, 40);
        &:hover {
          color: #ec1f23;
        }
        [dir="rtl"] & {
          right: unset;
          left: 10px;
        }
      }
    }
    .blog-tab {
      display: none;
      &.active {
        display: block;
      }
    }
    .blog-cats {
      background-color: rgb(245, 245, 245);
      padding: 35px 35px;
      margin: 0 0 30px 0;
      .cats-title {
        font-family: r-bold;
        font-size: 18px;
        color: rgb(52, 52, 51);
        margin: 0 0 20px 0;
        [dir="rtl"] & {
          font-family: ar-bold;
        }
      }
      .cat-select {
        >li {
          font-family: reg;
          font-size: 16px;
          color: #333;
          margin: 0 0 15px 0;
          >a {
            transition: all .3s ease-in-out;
            &:hover {
              color: #ec1f23;
            }
            &.active {
              color: #ec1f23;
            }
          }
          [dir="rtl"] & {
            font-family: ar-reg;
          }
        }
      }
    }
    .blog-card {
      background-image: url("../images/blog-page-back.png");
      background-size: contain;
      background-position: left;
      background-repeat: no-repeat;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.07);
      width: 100%;
      height: 260px;
      margin: 0 0 30px 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .blog-img {
        position: relative;
        overflow: hidden;
        // height: 250px;
        background-color: #fff;
        img {
          transition: all .5s ease-in-out;
        }
      }
      .blog-body {
        padding: 35px 25px;
        width: calc(100% - 360px);
        height: 100%;
        background-color: #fff;
        .date {
          font-family: reg;
          font-size: 11px;
          color: #7c7c7c;
          font-style: italic;
