          font-size: 12px;
          color: rgb(236, 31, 35);
          font-style: italic;
          margin: 20px 0 0 0;
          [dir="rtl"] & {
            font-family: ar-reg;
            letter-spacing: normal;
          }
        }
      }
      &:hover {
        .blog-img {
          img {
            transform: scale(1.05);
            opacity: 0.780;
          }
        }
      }
    }
  }
