/*------ contact ------*/
.contact-page {
  .contact-page-content {
    margin: 50px 0 150px 0;
    .contact-info-container {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      position: relative;
      .contact-info {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        z-index: 9;
        width: 487px;
        height: 297px;
        background-color: #ec1f23;
        color: rgb(255, 255, 255);
        padding: 40px 50px;
        [dir="rtl"] & {
          left: unset;
          right: 0;
        }
        .title {
          >h4 {
            font-family: r-bold;
            font-size: 24px;
            margin: 0 0 25px 0;
            letter-spacing: 1.1;
            [dir="rtl"] & {
              font-family: ar-bold;
            }
          }
        }
        .contact-info-list {
          >li {
            font-family: reg;
            font-size: 16px;
            margin: 0 0 13px 0;
            [dir="rtl"] & {
              font-family: ar-reg;
            }
            >a {
              display: flex;
              justify-content: flex-start;
              align-items: center;
              >i {
                width: 17px;
                height: 17px;
                transform: rotateY(180deg);
                display: flex;
                justify-content: center;
                align-items: center;
              }
              >span {
                // padding: 0 0 0 20px;
                padding: 2px 0 0 20px;
                [dir="rtl"] & {
                  padding: 2px 20px 0 0;
                }
              }
            }
          }
        }
      }
      .location {
        width: 487px;
        height: 360px;
      }
    }

    .owl-carousel {
      position: relative;
      .owl-nav {
        display: block;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: row-reverse;
        margin: 25px 0 0 0;
        [dir="rtl"] & {
          justify-content: flex-start;
          flex-direction: row;
        }
        .owl-prev, .owl-next {
          width: 32px;
          height: 32px;
          background-color: #292928;
          color: rgb(255, 255, 255);
          transition: all .3s ease-in-out;
          margin: 0 4px;
          font-size: 12px;
          &:hover {
            background-color: #ffffff;
            color: #ec1f23;
          }
        }
      }
    }
    .contact-form {
      margin: 90px auto 0 auto;
      max-width: 460px;
      .form-title {
        >h3 {
          font-family: e-bold;
          font-size: 36px;
          color: #292928;
          text-align: center;
          text-transform: uppercase;
          margin: 0 0 25px 0;
          [dir="rtl"] & {
            font-family: ar-bold;
          }
        }
      }
      input, textarea {
        width: 100%;
        height: 60px;
        padding: 15px 25px;
        margin: 0 0 10px 0;
        outline: none;
        resize: none;
        border-style: solid;
        border-width: 1px;
        border-color: #e6e6e6;
        background-color: rgb(255, 255, 255);
        transition: all .3s ease-in-out;
        font-family: reg;
        font-size: 15px;
        color: rgb(52, 52, 51);
        [dir="rtl"] & {
          font-family: ar-reg;
        }
        &::placeholder {
          font-family: reg;
          font-size: 15px;
          color: #b8b7b7;
          [dir="rtl"] & {
            font-family: ar-reg;
          }
        }
        &:hover, &:focus {
          border-color: #ec1f23;
        }
      }
      textarea {
        height: 114px;
        resize: vertical;
      }
      .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width: 100%;
        height: 60px;
        margin: 0 0 10px 0;
      }
      .bootstrap-select>.dropdown-toggle.bs-placeholder, .bootstrap-select>.dropdown-toggle.bs-placeholder:active, .bootstrap-select>.dropdown-toggle.bs-placeholder:focus, .bootstrap-select>.dropdown-toggle.bs-placeholder:hover {
        height: 100%;
        padding: 15px 25px;
        background-color: #ffffff;
        outline: none !important;
        border-color: #e6e6e6;
        border-radius: 0;
        transition: all .3s ease-in-out;
      }
      .bootstrap-select>.dropdown-toggle.bs-placeholder:active, .bootstrap-select>.dropdown-toggle.bs-placeholder:focus, .bootstrap-select>.dropdown-toggle.bs-placeholder:hover {
        border-color: #ec1f23;
      }
      .bootstrap-select .dropdown-toggle .filter-option {
        font-family: reg;
        font-size: 15px;
        color: rgb(184, 183, 183);
        padding: 3px 0 0 0;
        [dir="rtl"] & {
          font-family: ar-reg;
          text-align: right;
        }
      }
      .bootstrap-select .dropdown-toggle .caret {
        right: 25px;
        font-size: 15px;
        color: #b8b7b7;
        [dir="rtl"] & {
          right: unset;
          left: 25px;
        }
      }
      .bootstrap-select .dropdown-menu li a {
        padding: 10px 25px;
        outline: none;
      }
      .dropdown-menu>.active>a, .dropdown-menu>.active>a:focus, .dropdown-menu>.active>a:hover {
        background-color: #ec1f23;
      }
      .send-btn {
        >a {
          display: block;
          width: 154px;
          height: 45px;
          background-color: #ec1f23;
          font-family: md;
          font-size: 16px;
          color: rgb(255, 255, 255);
          text-transform: uppercase;
          margin: 10px auto 0 auto;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: all .3s ease-in-out;
          [dir="rtl"] & {
            font-family: ar-md;
          }
          &:hover {
            background-color: #000;
          }
        }
      }
    }
  }
}
@media (max-width: 992px) {
  .contact-page .contact-page-content {
    margin: 30px 0 40px 0;
    .contact-info-container {
      flex-direction: column-reverse;
        .location {
          width: 100%;
          height: 260px;
        }
      .contact-info {
        width: 100%;
        height: auto;
        position: relative;
        top: unset;
        left: unset;
        right: unset;
        transform: unset;
        margin: 20px 0;
        padding: 20px 30px;
        .title {
          >h4 {
            font-size: 18px;
          }
        }
      }
    }
    .contact-form {
      margin: 30px 0 0 0;
    }
  }
}
